<template>
	<div  class="CheckOrder_check">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>按装运复核-复核</span></header>
		<div class="container">
			<div class="import">				
				<span>装运号</span>
				<input style="background-color: #CCCCCC;"  ref="OrderKey"  v-model="OrderKey" readonly></input>													
			</div>
			<div class="import">				
				<span>箱号</span>
				<input   style="background-color: #CCCCCC;"   ref="CaseNo"  v-model="CaseNo" readonly></input>			
			</div>
			<div class="qty">				
				<span>数量</span>
				<input ref="Qty" @keyup.enter="toSku"  v-model="Qty" ></input>
				<input style="background-color: #CCCCCC;" ref="Susr2"  v-model="Susr2" readonly></input>
			</div>
			<div class="import">				
				<span>商品代码</span>
				<input  placeholder="请输入内容" @keyup.enter="checkSku"   ref="Sku"  v-model="Sku" ></input>
				<i v-if="Sku" @click="clearInput('Sku')" class="iconfont icon-cuowu"></i>									
			</div>
			<div class="goods_name">
				<span>商品名称</span>
				<textarea v-model="Descr" ref="Descr"  readonly></textarea>
			</div>
			<div class="checkQty">				
				<span>已复核</span>
				<input ref="CheckedQty"  v-model="CheckedQty" ></input>
				<span class="daiCheck">待复核</span><input style="background-color: #CCCCCC;" ref="PreCheckQty"  v-model="PreCheckQty" readonly></input>
			</div>	
			<div class="import">				
				<span>总待复核</span>
				<input style="background-color: #CCCCCC;"  ref="SumPreCheckQty"  v-model="SumPreCheckQty" readonly></input>													
			</div>	
			<div class="g_doubleBtn_chckOrder">				
				<div @click="check">复核完成</div>
				<div @click="closeCase">关箱</div>	
				<div @click="checkSku">确认</div>
			</div>
		</div>
		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"CheckLoad_check",
		data(){
			return{
				OrderKey:'',
				CaseNo:'',
				Qty:'1',
				Susr2:'单位',//单位
				Sku:'',
				Descr:'',//商品名称
				CheckedQty:'',//已复核
				SumPreCheckQty:'',//总待复核				
				SumQty:'',//商品 总发运数
				StorerKey:'',
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",		
//				CheckOrder:"",
				BoxType:'ByLoad',
				SumCheckedQty:'',//已复核总数
			}
		},
		computed:{
			PreCheckQty:function(){
				return this.SumQty-this.CheckedQty;
			}
		},
		mounted() {
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
			this.$refs['Sku'].focus();
		},
		created(){
			var that = this;
			// SO单号
			if(this.$route.query.OrderKey){
				that.OrderKey = that.$route.query.OrderKey;				
			}
			// 箱号
			if(this.$route.query.CaseNo){
				that.CaseNo = that.$route.query.CaseNo;				
			}
			
			// 客户代码
			if(this.$route.query.StorerKey){
				that.StorerKey = that.$route.query.StorerKey;				
			}
			
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				this.ToTakeDelivery();
			},
			//返回上一页  
			toIndex:function(){
				this.$router.push({
					name:"CheckLoad",
					query:{
								OrderKey : this.OrderKey,
								   CaseNo: this.CaseNo,
							}
				})
			},	
			toSku:function(){
				this.$refs['Sku'].focus();
			},
			//复核商品  按装运
			checkSku:function(){
				var that = this;
			  if(this.Qty =='') {
 				that.msg = "数量不能为空";
				that.tipsShow("Qty");
				that.inputFocus = "Qty";
				return false;
 			     }
			   if(this.Sku =='') {
 				that.msg = "商品代码不能为空";
				that.tipsShow("Sku");
				that.inputFocus = "Sku";
				return false;
 			     }
				var currentUrl = this.HOST + "RFCheckSku.do";
				var postData = {
					OrderKey : that.OrderKey,
					 BoxType  : that.BoxType,
					      Sku : that.Sku,
					      Qty : that.Qty,
					   CaseNo : that.CaseNo,
					StorerKey : that.StorerKey,
				}
				this.httpRequest(currentUrl,postData,function(res){
					if(res.data.data.length){
//						that.$store.state.SkuBound_SelectSkuData = res.data.data;
						that.Descr=res.data.data[0].Descr;
						that.CheckedQty=res.data.data[0].SumCheckedQty;
						that.SumQty=res.data.data[0].SumQty;
						that.SumPreCheckQty=res.data.data[0].SumPreCheckQty;
						that.Susr2=res.data.data[0].Susr2;
						that.Sku='';
						that.$refs['Sku'].focus();
					}else{

						that.tipsShow("Sku");

						that.msg = "未查询到数据";

						that.inputFocus = "Sku";
					}
				},"Sku")
			},
			// 复核完成  获取复核单总数
			check:function(){

				var that = this;

				var currentUrl = this.HOST + "RFCheck.do";

				var postData = {
					OrderKey : that.OrderKey,
					BoxType:that.BoxType,
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){
						var sumQty=0;					
						for(var i=0;i<res.data.data.length;i++){
							sumQty=sumQty+res.data.data[i].SumQty;
						}											
                       if(sumQty - res.data.data[0].SumCheckedQty ==0){
                       	that.$router.push({
						name:"CheckLoad",
						})                 	
                       }else{
                       	that.tipsShow("");
                       	that.msg = "你还有商品未复核,未复核数量："+(sumQty - res.data.data[0].SumCheckedQty);
						that.inputFocus = "Sku";
                       }
                       
					}else{

						that.tipsShow("");

						that.msg = "未查询到数据";

						that.inputFocus = "Orderkey";
					}
				},"OrderKey")
			},
			//关箱
			closeCase:function(){
				var that = this;
				var currentUrl = this.HOST + "RFCheckSku.do";
				var postData = {
					OrderKey : that.OrderKey,
					 BoxType  : that.BoxType,
					      Sku : that.Sku,
					      Qty : that.Qty,
					   CaseNo : that.CaseNo,
					StorerKey : that.StorerKey,
				}				
				if(this.Qty !='' && this.Sku !=''){
				this.httpRequest(currentUrl,postData,function(res){
					if(res.data.data.length){
						that.Descr=res.data.data[0].Descr;
						that.CheckedQty=res.data.data[0].SumCheckedQty;
						that.SumQty=res.data.data[0].SumQty;
						that.SumPreCheckQty=res.data.data[0].SumPreCheckQty;
						that.Susr2=res.data.data[0].Susr2;
					 	that.$router.push({
						name:"CheckLoad",
						query:{
								OrderKey : that.OrderKey,
								       CaseNo: that.CaseNo,
							}
						})					
					
					}else{

						that.tipsShow("Sku");

						that.msg = "未查询到数据";

						that.inputFocus = "Sku";
					}
				},"Sku")	
				}else{
					this.toIndex();
				}				
			}
			
		}
	}
</script>
