<template>
	<div  class="SkuBound">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>绑定商品条码</span></header>
		<div class="container">
			<div class="import">
				<div class="top">
				<span>客户</span>
				<input  placeholder="请输入内容" @keyup.enter="skipSku"   ref="StorerKey"  v-model="StorerKey" ></input>
				<i v-if="StorerKey" @click="clearInput('StorerKey')" class="iconfont icon-cuowu"></i>			
				<i class="iconfont icon-sousuo"  @click="toSelectStorerKey" ></i>
			</div>
			</div>
			<div class="import">
				<div class="top">
				<span>商品代码</span>
				<input  placeholder="请输入内容" @keyup.enter="skipALtSku"      ref="Sku"  v-model="Sku" ></input>
				<i v-if="Sku" @click="clearInput('Sku')" class="iconfont icon-cuowu"></i>
				<i class="iconfont icon-sousuo"  @click="toSelectSku" ></i>
			</div>
			</div>
			
			<div class="goods_name">
				<span>商品名称</span>
				<textarea style="background-color: #CCCCCC;" v-model="Descr" ref="Descr"  readonly></textarea>
			</div>
			
			<div class="import">
				<span>商品条码</span>
				<input  placeholder="请输入内容" @keyup.enter="skipAltSkuType"  ref="ALtSku"  v-model="ALtSku" ></input>
				<i v-if="ALtSku" @click="clearInput('ALtSku')" class="iconfont icon-cuowu"></i>
			</div>
			
			<div class="import">				
				<span>条码类型</span>
				<select ref="AltSkuType" v-model="AltSkuType">
					<option value="0" selected="selected">商品统一条码</option>
					<option value="1" >生产商条码</option>
					<option value="2" >零售条码</option>
				</select>			
			</div>				
				<span class="g_singleBtn" @click="altSkuBound">确认</span>
		</div>
		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"SkuBound",
		data(){
			return{
				StorerKey:'',
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",
				SkuBound:"",
				Sku:"",
				Descr:"",
				ALtSku:"",
				AltSkuType:"0",
				StoreKey_select:""
			}
		},
		mounted() {			
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
			if(!this.StorerKey){
			this.$refs['StorerKey'].focus();
			}
			if(this.StorerKey){
				this.$refs['Sku'].focus();
			}
			if(this.StorerKey && this.Sku){
				this.$refs['ALtSku'].focus();
			}
		},
		created(){

			var that = this;
			
			// 客户代码
			if(this.$route.query.StorerKey){

				that.StorerKey = that.$route.query.StorerKey;
			}

			// 在输入框填写  其他页面传来的商品代码
			if(this.$route.query.Sku){
				that.Sku = that.$route.query.Sku;				
			}
			
			// 在输入框填写  其他页面传来的商品名称
			if(this.$route.query.Descr){
				that.Descr = that.$route.query.Descr;				
			}
			
			
			
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				this.ToTakeDelivery();
			},
			//调到商品代码
			skipSku(){

				this.$refs['Sku'].focus();
			},
			skipAltSkuType(){
				this.$refs['AltSkuType'].focus();
			},
			//调到商品条码
			skipALtSku(){
				
				var that = this;

				var currentUrl = this.HOST + "getDescrBySku.do";

				var postData = {
					StorerKey : this.StorerKey,
					Sku : this.Sku
				}

				this.httpRequest(currentUrl,postData,function(res){
					if(res.data.Msg !='当前客户下,该商品不存在,请核实后再输入！'){
						that.Descr=res.data.Msg;
						that.$refs['ALtSku'].focus();
					}else{
						that.tipsShow("Sku");

						that.msg = res.data.Msg;

						that.inputFocus = "Sku";												
					}
				},"Sku")
								
			},
			//返回上一页 首页
			toIndex:function(){
				this.$router.push({
					name:"Index",
				})
			},
			// 跳转到 选择客户页面
			toSelectStorerKey:function(){

				var that = this;

				var currentUrl = this.HOST + "RFAltSkuBoundStorerKey.do";

				var postData = {
					StorerKey : this.StorerKey,
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){

						that.$store.state.RFPick_SelectOrderData = res.data.data;

						that.$router.push({
							name:"StoreKey_select",
							query:{
								StorerKey : that.StorerKey,
							}
						})
						
					}else{

						that.tipsShow("StorerKey");

						that.msg = "未查询到数据";

						that.inputFocus = "StorerKey";
					}
				},"StorerKey")
			},
			
			//跳转到商品选择页面
			toSelectSku:function(){

				var that = this;				

				var currentUrl = this.HOST + "RFAltSkuBoundSku.do";

				var postData = {
					StorerKey : this.StorerKey,
					Sku:this.Sku,
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){

						that.$store.state.SkuBound_SelectSkuData = res.data.data;

						that.$router.push({
							name:"Sku_select",
							query:{
								StorerKey : that.StorerKey,
								       Sku: that.Sku,
							}
						})
					}else{

						that.tipsShow("Sku");

						that.msg = "未查询到数据";

						that.inputFocus = "Sku";
					}
				},"Sku")
			},
			
			//商品条码绑定确定
			altSkuBound:function(){

				var that = this;
				if(this.StorerKey =='') {
 				that.msg = "客户代码不能为空";
				that.tipsShow("StorerKey");
				that.inputFocus = "StorerKey";
 			   }else
				if(this.Sku =='') {
 				that.msg = "商品代码不能为空";
				that.tipsShow("Sku");
				that.inputFocus = "Sku";
 			   }else
				if(this.ALtSku =='') {
 				that.msg = "商品条码不能为空";
				that.tipsShow("ALtSku");
				that.inputFocus = "ALtSku";
 			  }else{
				var currentUrl = this.HOST + "RFAltSkuBound.do";
				var postData = {
					StorerKey : this.StorerKey,
					Sku:this.Sku,
					ALtSku:this.ALtSku,
					AltSkuType:this.AltSkuType
				}

				this.httpRequest(currentUrl,postData,function(res){
					that.StorerKey="";
					that.Sku="";
					that.ALtSku="";
					that.Descr="";
				},)
				
				}
			}
		}
	}
</script>
<style scoped lang="less">
.SkuBound{
	&>.container{
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}		
		
		&>div.import{
			width:100%;
			height: 80/50rem;
			/*padding: 25/50rem 20/50rem 0;*/
			margin-bottom: 20/50rem;
			box-sizing: border-box;
			position: relative;
			overflow: hidden;
			span{
				width: 130/50rem;
				text-align: right;
				float: left;
				margin-right: 10/50rem;
			}
			input{
				margin-left: 10/50rem;
				width:445/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			select{
				margin-left: 10/50rem;				
				width:470/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			textarea{
				margin-left: 10/50rem;
				margin-top: 15/50rem;
				margin-bottom: 15/50rem;
				width:465/50rem;	
				height:80/50rem;
				float: left;
			}						
			i{
				position:absolute;
				right:90/50rem;
				font-size: 30/50rem;
				top: 36/50rem;
			}
			i.icon-sousuo{
				right:30/50rem;
				font-size: 40/50rem;
				top: 30/50rem;
			}
								    				
			&>div.top{
				margin-top: 20/50rem;
			}
			/*&>div.goods_name{
				height: 80/50rem;
				textarea{
					height: 80/50rem;
				textarea{
				margin-left: 10/50rem;
				margin-top: 15/50rem;
				margin-bottom: 15/50rem;
				width:465/50rem;	
				height:80/50rem;
				float: left;
			}	
				}
			}			*/
		}
		&>div.goods_name{
			width:100%;
			height: 120/50rem;
			/*padding: 25/50rem 20/50rem 0;*/
			margin-bottom: 20/50rem;
			box-sizing: border-box;
			position: relative;
			overflow: hidden;
			span{
				width: 130/50rem;
				text-align: right;
				float: left;
				margin-right: 10/50rem;
			}
			textarea{
				margin-left: 10/50rem;
				margin-top: 20/50rem;
				margin-bottom: 20/50rem;
				width:465/50rem;	
				height:80/50rem;
				float: left;
			}
		}
	}
}
@media screen and (max-width: 320px){
	.SkuBound{
		&>.container{
			font-size: 38/50rem;
			input::placeholder{
				font-size: 35/50rem;
			}
			input{
				font-size: 35/50rem;
			}
			&>div.import{
				height: 92/50rem;
				padding: 0;
				span{
					width: 160/50rem;
					height: 65/50rem;
					line-height: 65/50rem;
					padding-right: 15/50rem;
					padding-left: 10/50rem;
				}
				
				input{
					width:445/50rem;	
					height:65/50rem;
					line-height: 65/50rem;
					/*margin-left: 10/50rem;*/
					margin: 0;
				}
				select{
				margin-left: 0/50rem;
				width:470/50rem;	
				height:65/50rem;
				line-height: 65/50rem;
				padding:0 10/50rem;
			}
				i{
					position:absolute;
					right:120/50rem;
					font-size: 42/50rem;
					top: 26/50rem;
				}
				i.icon-sousuo{
					right:25/50rem;
					font-size:50/50rem;
					top: 28/50rem;
				}				
				
			}
			
			&>div.goods_name{				
				height: 120/50rem;
			  span{
					width: 160/50rem;
					height: 65/50rem;
					line-height: 65/50rem;
					padding-right: 5/50rem;
					padding-left: 10/50rem;
				}									
				textarea{
				/*margin-left: 10/50rem;*/
				margin-top: 20/50rem;
				margin-bottom: 15/50rem;
				width:465/50rem;	
				height:90/50rem;
				float: left;
			}	
				
			}
			
			&>div.top{
				margin-top: 20/50rem;
			}
			&>div.butto{
				margin-bottom: 0;
			}
		}
	}
}
@media screen and (max-width: 277px){
	.SkuBound{
		&>.container{
			font-size: 42/50rem;
			input::placeholder{
				font-size: 40/50rem;
			}
			input{
				font-size: 40/50rem;
			}
			&>div.import{
				height: 120/50rem;
				line-height: 100/50rem;	
				span{
					width: 185/50rem;
					height: 65/50rem;
					line-height: 70/50rem;
					padding-right: 5/50rem;
					padding-left: 5/50rem;
				}
							
				input{					
					width:430/50rem;	
					height:70/50rem;
					margin: 0;
					padding: 0;
				}
				select{			
				width:430/50rem;	
				height:70/50rem;
				margin: 0;
				padding:0;
			}
				i{
					position:absolute;
					right:110/50rem;
					font-size: 48/50rem;
					top: 26/50rem;
				}
				i.icon-sousuo{
					right:20/50rem;
					font-size:56/50rem;
					top: 28/50rem;
				}
				
			}
			
			&>div.goods_name{
				height: 120/50rem;
			  span{
					width: 185/50rem;
					height: 65/50rem;
					line-height: 70/50rem;
					/*padding-right: 5/50rem;*/
					padding-left: 5/50rem;
				}									
				textarea{
				margin-top: 20/50rem;
				margin-bottom: 15/50rem;
				margin-left: 0;
				width:430/50rem;	
				height:90/50rem;
				float: left;
			}
			}
			&>div.top{
				margin-top: 20/50rem;
			}
		}
	}
}
</style>


