<template>
	<div class="StoreKey_select">
		<header><i class="iconfont icon-xiangzuo1" @click="toReceipt"></i><span>绑定商品条码-选择客户</span></header>
		<div class="container">
			<table cellspacing="0">
				<thead>
					<tr>
						<td>选择</td>
						<td>客户代码</td>
						<td>客户名称</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in RFPick_SelectOrderData'  :id="index"  :key='index'  @click="selectData(index)">
						<td class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></td>
						<td>{{dataItem.StorerKey}}</td>
						<td>{{dataItem.StorerName}}</td>
					</tr>
				</tbody>
			</table>
			<div class="kong"></div>
			<p class="positionFixed" ><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"StoreKey_select",
		data(){
			return{
				RFPick_SelectOrderData:[],//列表数据
				detail_item:0,//默认为第一个
			}
		},
		created(){
			console.log(this.$store.state.RFPick_SelectOrderData)
			if(this.$store.state.RFPick_SelectOrderData.length){
				var RFPick_SelectOrderData = this.$store.state.RFPick_SelectOrderData;
				this.RFPick_SelectOrderData = RFPick_SelectOrderData;
			}
		},
		methods:{
			// 返回上一页 
			toReceipt:function(){

				var that = this;

				this.$router.push({
					name:"SkuBound",
					query:{
						StorerKey:that.$route.query.StorerKey
					}
				})
			},
			//点击选择具体客户
			selectData:function(idx){
				this.detail_item = idx;				
			},
			// 点击确认按钮 跳转到绑定商品条码页面
			selectData_submit:function(){
				var StorerKey = this.RFPick_SelectOrderData[this.detail_item].StorerKey//客户代码
				this.$router.push({
					name:"SkuBound",
					query:{
						StorerKey:StorerKey
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.RFPick_select{
		&>.container{
			width:100%;
			padding-bottom: 130rpx;
			table{
				tr{
					td:nth-child(2),td:nth-child(3){
						min-width: 150/50rem;
					}
					td:nth-child(4),td:nth-child(5){
						min-width: 115/50rem;
					}
					td:nth-child(6){
						min-width: 200/50rem;
					}
					td:last-child{
						min-width: 400/50rem;
					}
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
		}
	}
	@media screen and (max-width: 250px){
		.RFPick_select{
			&>.container{
				table{
					tr{
						td{
							min-width: 100/50rem;
						}
						td:nth-child(3){
							min-width: 200/50rem;
						}
						td:nth-child(4),td:nth-child(5){
							min-width: 150/50rem;
						}
						td:nth-child(6){
							min-width: 240/50rem;
						}
						td:nth-child(7){
							min-width: 480/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
</style>


