<template>
	
	<div class="indexContainer">
		<header><span>首页</span></header>
		<div class="container">
			<router-link v-if="Receipt" to="/Receipt">
				<div  class="item">
					<div class="iconfont icon-cangku"></div>
					<div>收货</div>		
				</div>
			</router-link>
			<router-link  v-if="ReceiptNumCollect"  :to="{path:'/ConsignmentIndex',query: {name: 'Receipt'}}">
				<div class="item">
					<div class="iconfont icon-querenshouhuo_fuzhi"></div>
					<div>收货扫码</div>
				</div>
			</router-link>
			<router-link  v-if="putaway"  to="/Putaway">
				<div  class="item">
					<div class="iconfont icon-ccgl-shangjiasaomiao-5"></div>
					<div>上架</div>
				</div>
			</router-link>
			<router-link  v-if="inventoryQuery"  to="/InventoryQuery">
				<div  class="item">
					<div class="iconfont icon-chaxun"></div>
					<div>查询</div>
				</div>
			</router-link>
			<router-link  v-if="Replenishment"  to="/ReplenishIndex">
				<div class="item">
					<div class="iconfont icon-ccgl-buhuoguanli-7"></div>
					<div>补货</div>
				</div>
			</router-link>
			<router-link  v-if="RFInvMove"  to="/RFInvMove">
				<div class="item">
					<div class="iconfont icon-kucunyidong"></div>
					<div>移动</div>
				</div>
			</router-link>
			<router-link  v-if="RFPick"  to="/RFPick">
				<div class="item">
					<div class="iconfont icon-jianhuo"></div>
					<div>拣货</div>
				</div>
			</router-link>
			<router-link  v-if="CycleCount"  to="/CycleCount">
				<div class="item">
					<div class="iconfont icon-pandianxiangqing"></div>
					<div>盘点</div>
				</div>
			</router-link>
			<router-link   v-if="RFOrderShip"  to="/RFOrderShip">
				<div class="item">
					<div class="iconfont icon-fayunshuliang"></div>
					<div>发运</div>
				</div>
			</router-link>
			<router-link  v-if="shipmentNumCollect"  :to="{path:'/ConsignmentIndex',query: {name: 'Consignment'}}">
				<div class="item">
					<div class="iconfont icon-kucunpandian"></div>
					<div>发货扫码</div>
				</div>
			</router-link>
			<router-link   to="/CheckOrder">
				<div  class="item">
					<div class="iconfont icon-chaxun"></div>
					<div>按单复核</div>
				</div>
			</router-link>
			<router-link   to="/CheckLoad">
				<div  class="item">
					<div class="iconfont icon-chaxun"></div>
					<div>按装运复核</div>
				</div>
			</router-link>
			<router-link  to="/SkuBound">
				<div class="item">
					<div class="iconfont icon-pandianxiangqing"></div>
					<div>条码绑定</div>
				</div>
			</router-link>
		</div>
		<div class="exit"  @click="exit">注销</div>
	</div>
</template>
<script type="text/javascript">

import "../../static/font/iconfont.css"
export default{
	name:"Index",
	data(){
		return{
			Receipt:false,
			ReceiptNumCollect:false,
			putaway:false,
			inventoryQuery:false,
			Replenishment:false,
			RFInvMove:false,
			CycleCount:false,
			RFPick:false,
			RFOrderShip:false,
			shipmentNumCollect:false,
			limitControlData:[],//权限列表
		}
	},
	created(){

		var that = this;

		var SummaryUrl = this.HOST + "RFAuth.do";

		var sum_Data = {
			UserID : that.$store.state.UserID,//用户名
			WarehouseCode : that.$store.state.WarehouseCode,//仓库名称
		}

		var pageData = ["Receipt","ReceiptNumCollect","putaway","inventoryQuery","Replenishment","RFInvMove","CycleCount","RFPick","RFOrderShip","shipmentNumCollect"];

		if(that.$store.state.limitControlList.length){

			that.limitControlData = that.$store.state.limitControlList;

			for(var i = 0;i < pageData.length ; i++){

				for(var j=0 ; j < that.limitControlData.length;j++){

					if(that.limitControlData[j].module_name.split("/")[2].split(".")[0] == pageData[i]){
						that[pageData[i]] = true;
					}
				}
			}
		}else{

			this.httpRequest(SummaryUrl,sum_Data,function(res){

				that.limitControlData = res.data.data;

				that.$store.state.limitControlList = res.data.data;

				for(var i = 0;i < pageData.length ; i++){

					for(var j=0 ; j < that.limitControlData.length;j++){

						if(that.limitControlData[j].module_name.split("/")[2].split(".")[0] == pageData[i]){
							that[pageData[i]] = true;
						}
					}
				}
			})
		}
	},
	methods:{
		// 退出登录
		exit:function(){

			// 仓库
			this.$store.state.WarehouseCode = "";

			// 用户名
			this.$store.state.UserID = "";

			// 权限列表
			this.$store.state.limitControlList = [];

			//跳转到登录页面
			this.$router.push({
				name:"Login",
				query:{
				}
			})
			// this.$router.push({
			// 	name:"Chart",
			// 	query:{
			// 	}
			// })
		}
	}
}
	
</script>
<style  scoped lang="less">
	.indexContainer{
		width:100%;
		height: 100%;
		.container{
			margin: 150/50rem auto 0;
			height: 100%;
			width: 500/50rem;
			overflow: hidden;
			.item{
				border-radius:15/50rem;
				float: left;
				width: 200/50rem;
				box-sizing: border-box;
				text-align:center;
				border: 1/50rem solid #ddd;
				padding: 20/50rem 0;
				margin-bottom: 50/50rem;
				margin-right: 100/50rem;
				font-size: 32/50rem;
				color:#000;
				.iconfont{
					font-size: 58/50rem;
					margin-bottom: 10/50rem;
					color:#2B5FD8;
				}
			}
			&>a:nth-child(2) .item,&>a:nth-child(4) .item,&>a:nth-child(6) .item,&>a:nth-child(8) .item,&>a:nth-child(10) .item,&>a:nth-child(12) .item,&>a:nth-child(14) .item{
				margin-right: 0;
			}

		}
		.exit{
			width:600/50rem;
			height: 70/50rem;
			background-color: #285DCE;
			text-align: center;
			line-height: 70/50rem;
			border-radius: 10/50rem;
			color: #fff;
			font-size: 29/50rem;
			margin: 30/50rem auto;
		}
	}

	@media screen and (max-width: 500px) and (min-width: 479px) {	
		.indexContainer{
			.container{
				.item{
					border-radius:15/50rem;
					width: 185/50rem;
					padding: 20/50rem 0;
					margin-bottom: 30/50rem;
					margin-right: 130/50rem;
					font-size: 26/50rem;
					color:#000;
					.iconfont{
						font-size: 40/50rem;
						margin-bottom: 0;
						color:#2B5FD8;
					}
				}
			}
		}
	}
	@media screen and (max-width: 374px) {	
		.indexContainer{
			.container{
				.item{
					border: 3/50rem solid #ddd;
				}
			}
		}
	}

	@media screen and (max-width: 340px) and (min-width: 251px) {
		.indexContainer{
			.container{
				width: 100%;
				margin: 90/50rem auto 0;
				.item{
					width: 50%;
					border-radius:0;
					padding: 13/50rem 0;
					font-size: 35/50rem;
					margin: 0;
					border:none;
					border-bottom: 3/50rem solid #ddd;
					border-right:  3/50rem solid #ddd;
					color:#000;
					.iconfont{
						font-size: 50/50rem;
						margin-bottom: 0;
						color:#2B5FD8;
					}
				}
				&>a:nth-child(2) .item,&>a:nth-child(4) .item,&>a:nth-child(6) .item,&>a:nth-child(8) .item,&>a:nth-child(10) .item{
					border-right: none;
				}
			}
			.exit{
				height: 80/50rem;
				line-height: 80/50rem;
				font-size: 40/50rem;
				margin-top: 60/50rem;
			}
		}
	}
	@media screen and (max-width: 250px){
		.indexContainer{
			.container{
				margin: 108/50rem auto 0;
				.item{
					border-radius:0;
					margin-bottom:13/50rem;
					padding: 15/50rem 0;
					font-size: 45/50rem;
					color:#000;
					.iconfont{
						font-size: 60/50rem;
						margin-bottom: 10/50rem;
						color:#2B5FD8;
					}
				}
			}
			.exit{
				font-size: 45/50rem;
			}
		}
	}
</style>


