<template>
	<div class="CheckOrder_detail">
		<header><i class="iconfont icon-xiangzuo1" @click="toReceipt"></i><span>按装运复核-查看明细</span></header>
		<div class="container">
			<table cellspacing="0">
				<thead>
					<tr>
						<td>序号</td>
						<td>商品代码</td>
						<td>数量</td>
						<td>已复核数</td>
						<td>商品名称</td>
						<td>单位</td>
						<td>待复核数</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in CheckOrder_detailData'  :id="index"  :key='index'  @click="">
						<td>{{index+1}}</td>
						<td>{{dataItem.Sku}}</td>
						<td>{{dataItem.Qty}}</td>
						<td>{{dataItem.CheckedQty}}</td>
						<td>{{dataItem.Descr}}</td>
						<td>{{dataItem.Susr2}}</td>
						<td>{{dataItem.PreCheckQty}}</td>
					</tr>
				</tbody>
			</table>
			<div class="kong"></div>
			<p class="positionFixed" ><span class="g_singleBtn_check" @click="toReceipt">返回</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"CheckLoad_detail",
		data(){
			return{
				CheckOrder_detailData:[],//列表数据
				OrderKey:'',
			}
		},
		created(){
			// SO单号
			if(this.$route.query.OrderKey){

				this.OrderKey = this.$route.query.OrderKey;
			}
			
			console.log(this.$store.state.CheckOrder_detailData)

			if(this.$store.state.CheckOrder_detailData.length){

				var CheckOrder_detailData = this.$store.state.CheckOrder_detailData;

				this.CheckOrder_detailData = CheckOrder_detailData;

			}
		},
		methods:{
			// 返回上一页 录入单号 页面
			toReceipt:function(){

				var that = this;

				this.$router.push({
					name:"CheckLoad",
					query:{
						OrderKey:that.OrderKey,
					}
				})
			},
		}
	}
</script>
<style lang="less" scoped>
	.CheckOrder_detail{
		&>.container{
			width:100%;
			/*padding-bottom: 130rpx;*/
			table{
				tr{
					td:nth-child(2),td:nth-child(3){
						min-width: 150/50rem;
					}
					td:nth-child(4),td:nth-child(5){
						min-width: 115/50rem;
					}
					td:nth-child(6){
						min-width: 200/50rem;
					}
					td:last-child{
						min-width: 100/50rem;
					}
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
			.g_singleBtn_check{
	          width:50%;
	          margin:10/50rem auto;
	          display: block;
	          text-align: center;
	          background-color: #285DCE;
	          height:60/50rem;
	          color: #fff;
	          line-height: 70/50rem;
	          border-radius: 5/50rem;
            }		
		}
	}
	@media screen and (max-width: 250px){
		.CheckOrder_detail{
			&>.container{
				table{
					tr{
						td{
							min-width: 100/50rem;
						}
						td:nth-child(3){
							min-width: 200/50rem;
						}
						td:nth-child(4),td:nth-child(5){
							min-width: 150/50rem;
						}
						td:nth-child(6){
							min-width: 240/50rem;
						}
						td:nth-child(7){
							min-width: 480/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
</style>


